<template>
  <div id="top" class="content">
    <div class="filter">
      <select name="basic-dropdown" v-model="collection">
        <option>People</option>
        <option>Animals</option>
      </select>
    </div>
    <div class="image-part T">
      <button type="button" v-on:click.prevent="Tdecrement" class="btn btn-primary btn-left">prev</button>
      <div class="out">
        <img class="top" :src='path + collection + img + countT + type'/>
      </div>
      <button type="button" v-on:click.prevent="Tincrement" class="btn btn-primary btn-right">next</button>
    </div>
    <div class="image-part M">
      <button type="button" v-on:click.prevent="Mdecrement" class="btn btn-secondary btn-left">prev</button>
      <div class="out">
        <img class="middle" :src='path + collection + img + countM + type'/>
      </div>
      <button type="button" v-on:click.prevent="Mincrement" class="btn btn-secondary btn-right">next</button>
    </div>
    <div class="image-part B">
      <button type="button" v-on:click.prevent="Bdecrement" class="btn btn-tertiary btn-left">prev</button>
      <div class="out">
        <img class="bottom" :src='path + collection + img + countB + type'/>
      </div>
      <button type="button" v-on:click.prevent="Bincrement" class="btn btn-tertiary btn-right">next</button>
    </div>
  </div>
</template>

<script>
  export default {
    data: () => {
      return {
        totalImages: 8,
        countT: 1,
        countM: 1,
        countB: 1,
        path: '/assets/images/',
        img: '/image',
        type: '.png',
        collection: 'Animals'
      }
    },
    methods: {
      Tincrement () {
        (this.countT < this.totalImages) ? this.countT++ : this.countT = 1
      },
      Tdecrement () {
        (this.countT > 1) ? this.countT-- : this.countT = this.totalImages
      },
      Mincrement () {
        (this.countM < this.totalImages) ? this.countM++ : this.countM = 1
      },
      Mdecrement () {
        (this.countM > 1) ? this.countM-- : this.countM = this.totalImages
      },
      Bincrement () {
        (this.countB < this.totalImages) ? this.countB++ : this.countB = 1
      },
      Bdecrement () {
        (this.countB > 1) ? this.countB-- : this.countB = this.totalImages
      }
    }
  }
</script>

<style scoped>
  .content {
    height: 75vh;
    position: relative;
    box-shadow: -19px 22px 62px -7px rgba(0,0,0,0.75);
    border-radius: 8px;
  }
  .filter{
    position: absolute;
    left: -5vh;
    top: 2vh;
    box-shadow: -19px 22px 62px -7px rgba(0,0,0,0.75);
  }
  select {
    min-width: 8vw;
    height: 5vh;
  }
  .image-part {
    position: absolute;
  }

  .T {
    top: 0;
  }

  .M {
    top: 33%;
  }

  .B {
    top: 66%;
  }

  .out {
    width: 68vw;
    height: 25vh;
    overflow: hidden;
  }

  .out img {
    max-width: 100%;
    height: 300%;
    display: block;
    transition: display 5s ease;
  }

  img.top {
    margin: .1vh auto;
  }

  img.middle {
    margin: -24.8vh auto;
  }

  img.bottom {
    margin: -49.4vh auto;
  }

  .btn {
    position: absolute;
    top: 50%;
    color: black;
    box-shadow: -19px 22px 62px -7px rgba(0,0,0,0.75);
  }

  .btn-left {
    left: 5vw;
  }

  .btn-right {
    right: 5vw;
  }
  .btn-primary{
    background-color: red;
    border-color: red;
  }
  .btn-secondary{
    background-color: orange;
    border-color: orange;
  }
  .btn-tertiary{
    background-color: green;
    border-color: green;
  }
</style>



